import React from "react";
import {
	Box,
	Container,
	Input,
	Heading,
	SimpleGrid,
	Center,
	Spinner,
} from "@chakra-ui/react";
import { useSearchParams } from "react-router-dom";
import axios from "axios";
import config from "../config";
import { useForm } from "react-hook-form";
import Film from "../components/Film";

function Search() {
	let [searchParams, setSearchParams] = useSearchParams();
	const [results, setResults] = React.useState<any>([]);
	const [notFound, setNotFound] = React.useState<boolean>(false);
	const [isLoading, setIsLoading] = React.useState<boolean>(false);

	React.useEffect(() => {
		const search = async () => {
			setIsLoading(true);
			try {
				const { data: res } = await axios.get(
					encodeURI(
						`${config.search_url}/films/search-by-keyword?keyword=${
							searchParams.get("q") || ""
						}`
					),
					{
						headers: {
							"X-API-KEY": config.api_token,
						},
					}
				);
				setResults(res.films);
				if (res.films.length === 0) {
					setNotFound(true);
				} else {
					setNotFound(false);
				}
			} finally {
				setIsLoading(false);
			}
		};
		search();
	}, [searchParams.get("q")]);

	type FormData = {
		q: string;
	};
	const { register, handleSubmit } = useForm<FormData>();

	const onSubmit = handleSubmit((data) => {
		setSearchParams({ q: data.q });
	});

	return (
		<>
			<Container paddingTop={20}>
				<form onSubmit={onSubmit}>
					<Input
						defaultValue={searchParams.get("q") || ""}
						placeholder="Поиск"
						borderColor="#929292"
						borderRadius="20px"
						focusBorderColor="brand.500"
						_placeholder={{ fontSize: "16px", color: "white" }}
						{...register("q")}
					/>
				</form>
			</Container>
			<Box paddingTop={10}>
				{(!isLoading && (
					<>
						{(notFound && (
							<Center>
								<Heading>Ничего не найдено</Heading>
							</Center>
						)) || (
							<>
								<Heading fontWeight="700" size="lg">
									Результаты поиска
								</Heading>
								<Box paddingTop={5}>
									<SimpleGrid columns={[2, 3, 4, 5, 6, 7]} spacing="20px">
										{results.map((film: any) => (
											<Film film={film} isSearch />
										))}
									</SimpleGrid>
								</Box>
							</>
						)}
					</>
				)) || (
					<Box w="full">
						<Center>
							<Spinner color="brand.500" size="xl" />
						</Center>
					</Box>
				)}
			</Box>
		</>
	);
}

export default Search;
